//
// =Fonts & Bootstrap
// --------------------------------------------------

@import url('https://fonts.googleapis.com/css?family=Raleway:400,600,500,300,700|Lato:400,700');
@import 'icomoon';

@import "bootstrap/functions";
@import "bootstrap/variables";
@import "bootstrap/mixins";
@import "bootstrap/print";
@import "bootstrap/reboot";
@import "bootstrap/type";
@import "bootstrap/images";
// @import "bootstrap/code";
@import "bootstrap/grid";
// @import "bootstrap/tables";
@import "bootstrap/forms";
@import "bootstrap/buttons";
@import "bootstrap/transitions";
@import "bootstrap/dropdown";
// @import "bootstrap/button-group";
// @import "bootstrap/input-group";
@import "bootstrap/custom-forms";
@import "bootstrap/nav";
@import "bootstrap/navbar";
@import "bootstrap/card";
// @import "bootstrap/breadcrumb";
// @import "bootstrap/pagination";
@import "bootstrap/badge";
@import "bootstrap/jumbotron";
// @import "bootstrap/alert";
// @import "bootstrap/progress";
@import "bootstrap/media";
@import "bootstrap/list-group";
// @import "bootstrap/close";
@import "bootstrap/modal";
// @import "bootstrap/tooltip";
// @import "bootstrap/popover";
@import "bootstrap/carousel";
@import "bootstrap/utilities";

// Video JS
@import 'plugins/videojs-landio-skin';

// Animate CSS (none for mobile)
@media (min-width: 768px) {
  @import 'plugins/animate';
}


//
// =Waypoint animations
// --------------------------------------------------

@include media-breakpoint-up(md) {
  .wp {
    visibility: hidden;
  }

  .wp-2 {
    animation-delay: .1s;
  }

  .wp-3,
  .wp-8 {
    animation-delay: .2s;
  }

  .wp-5,
  .wp-6 {
    animation-timing-function: cubic-bezier(.2, 1, .3, 1);
    animation-delay: .3s;
  }

  .wp.animated {
    visibility: visible;
  }
}


//
// =Global
// --------------------------------------------------

$font-family-sans-serif-custom: 'Lato', sans-serif !default;

.bg-dark {
  color: #fff;

  a,
  a:focus,
  a:hover {
    color: #fff;
  }
}

.bg-dark-custom {
  margin-top: 1.1875rem;
  background-color: transparent !important;
}

.bg-white {
  background-color: #fff;
}

.section-intro,
.section-features,
.section-pricing,
.section-testimonials,
.section-text,
.section-signup {
  padding-top: 2.8125rem;

  @include media-breakpoint-up(md) {
    padding-top: 5.625rem;
  }
}

.section-features,
.section-pricing,
.section-text,
.section-news,
.section-signup {
  padding-bottom: 2.8125rem;

  @include media-breakpoint-up(md) {
    padding-bottom: 5.625rem;
  }
}

@include media-breakpoint-down(sm) {
  .p-y-lg {
    padding-top: map-get($spacers, 2) !important;
    padding-bottom: map-get($spacers, 2) !important;
  }
}

.textselect-off { user-select: none; }

.hidden-md-up {
  @include media-breakpoint-up(md) {
    display: none;
  }
}


//
// =Navbar
// --------------------------------------------------

.navbar-toggler {
  padding: 0;
  font-size: 2rem;
  border: 0;
  border-radius: 0;
  line-height: 2;
  order: 2;

  &:focus {
    outline: 0;
  }
}

.navbar-dark {
  .navbar-toggler {
    color: rgba(255, 255, 255, .75);
  
    &:focus,
    &:hover {
      color: #fff;
    }
  }

  .form-control { color: #fff; }
}

.navbar-toggler-custom {
  font-size: 1.55rem;
  order: 1;
}

.navbar {
  padding-top: .5rem;
  padding-bottom: .5rem;
  margin-top: 1.1875rem;
  font-size: .9375rem;
  font-weight: 600;
  line-height: 1.2;
  background-color: transparent;

  @include media-breakpoint-up(md) {
    padding-top: 0;
    padding-bottom: 0;
  }
}

.navbar-toggleable-custom .navbar-nav .nav-link {
  padding-top: 10px;
  padding-bottom: 10px;

  @include media-breakpoint-up(md) {
    padding: 1.9375rem 1rem;
  }
}

.nav-inline .nav-item + .nav-item,
.navbar-nav .nav-item + .nav-item {
  margin-left: 0;
}

.navbar-nav .nav-dropdown-search .nav-link {
  padding: 1.8125rem 1rem 1.9375rem;
}

.navbar-toggleable-custom {
  @include media-breakpoint-down(sm) {
    clear: both;
  }
}

#collapsingNavbar,
#collapsingNavbarInverse {
  @include media-breakpoint-up(md) {
    width: auto;
    display: block !important;
  }
}

@include media-breakpoint-down(sm) {
  .navbar-nav {
    .nav-item {
      display: block;
      float: none;
      margin: 0 0 1px;
      text-align: right;
      background-color: #fff;
    }
  }

  .navbar-dark .navbar-nav {

    .nav-item {
      background-color: transparent;
    }

    .nav-link {
      display: block;
      color: darken(#fff, 20%);

      &:focus,
      &:hover {
        color: #fff;
      }
    }

    .active .nav-link {
      color: #fff;
    }
  }

  .navbar-dark .navbar-nav {

    .nav-item {
      background-color: #fff;
      border-bottom: 3px solid $indigo;
    }

    .nav-link {
      padding: 1rem;
      color: lighten($gray-700, 20%);

      &:focus,
      &:hover {
        color: $gray-700;
      }
    }

    .active > .nav-link {
      color: map-get($theme-colors, primary) !important;
    }
  }
}

.fixed-top {
  position: absolute;
  top: 0;
  right: 0;
  left: 0;
}

.navbar-brand {
  padding: 1rem 0 1.0625rem;
  margin: 0;
}

.icon-logo { font-size: 45px; }

.navbar-nav .active > .nav-link {

  @include media-breakpoint-down(sm) {
    color: map-get($theme-colors, primary);
  }

  @include media-breakpoint-up(md) {
    position: relative;

    &:after {
      position: absolute;
      right: 1rem;
      bottom: 1.375rem;
      left: 1rem;
      height: 2px;
      content: '';
      background-color: rgba(66, 71, 83, .2);
    }
  }
}

.navbar-dark .navbar-nav .active > .nav-link {

  @include media-breakpoint-up(md) {
    position: relative;

    &:after {
      background-color: rgba(255, 255, 255, .2);
    }
  }
}

.navbar-divider {
  width: 5px;
  height: 5px;
  padding: 0;
  margin: 2.375rem .5rem;
  background: rgba(66,71,83,.2);
  border-radius: 50%;

  .navbar-dark & {
    background: rgba(255, 255, 255, .2);
  }
}

.dropdown.show .icon-search:before {
  content: '\e612'; // close search icon
}

.dropdown-menu {
  padding: 1.5rem 1.75rem;
  margin-top: 0;
  border: 0 none;
  box-shadow: 0 0 4px 0 rgba(0,0,0,.25);

  .media {
    padding-bottom: 1.25rem;
    margin-bottom: 1.25rem;
    color: $gray-700;
    border-bottom: 1px solid #e9eaec;
  }

  .text-muted {
    color: rgba(66,71,83,.4) !important;
  }
}

.dropdown-menu-animated {
  display: block;
  padding: 1.5rem 1.75rem;
  margin-top: 0;
  visibility: hidden;
  border: 0 none;
  box-shadow: 0 0 4px 0 rgba(0,0,0,.25);
  opacity: 0;
  transition: opacity .5s, transform .5s, visibility 0s .5s;
  transition-timing-function: cubic-bezier(.7, 0, .3, 1);
  transform: scale3d(.95, .95, 1) translate3d(0, -15px, 0);
  transform-origin: 100% 0;
}

.show {
  > .dropdown-menu-animated {
    visibility: visible;
    opacity: 1;
    transition: opacity .5s, transform .5s;
    transition-timing-function: cubic-bezier(.2, 1, .3, 1);
    transform: scale3d(1, 1, 1) translate3d(0, 0, 0);
  }
}

.dropdown-menu-custom {
  overflow-y: hidden;
  background-color: #fff;
  border: 0 none;

  .media {
    margin-bottom: 1.5rem;
    color: $gray-700;
  }

  .text-muted {
    color: rgba(66,71,83,.4) !important;
  }
}

.dropdown-item {
  padding: 0 0 .625rem;
  font-size: .8125rem;
  font-weight: 600;
  line-height: 1.1538;

  & + & { padding-top: .625rem; }

  &:hover,
  &:focus {
    background-color: transparent;
  }
}

.bg-dark .dropdown-item {
  color: $gray-700;

  &:focus,
  &:hover {
    color: map-get($theme-colors, primary);
    background-color: transparent;
  }
}

h5 {
  font-weight: 600;
  line-height: 1.2;
}

h6 {
  font-weight: 300;
  line-height: 1;
}

.btn-circle {
  display: block;
  width: 2.5rem;
  height: 2.5rem;
  padding: 0;
  font-size: 1.25rem;
  line-height: 2.2;
  color: #fff;
  text-align: center;
  border-radius: 50%;
  opacity: .8;

  &:focus,
  &:hover {
    color: #fff;

    @include gradient-directional(#663fb5, #663fb5);
  }
}

.has-gradient {
  @include gradient-directional(#663fb5, #2b8be3);
}

.dropdown-menu-search {
  padding: 0;
  margin: 0;
  box-shadow: none;

  @include media-breakpoint-up(md) {
    position: absolute;
    top: 1.375rem;
    right: 3.5rem !important;
    min-width: 22rem;
    background-color: transparent;
  }
}

.navbar-form .form-control {
  padding: 1rem 0 1rem 15px;
  font-size: 1rem; // prevents tap-zooming on mobile
  font-weight: 600;
  line-height: 1.2;
  background: transparent;
  border: 0;
  box-shadow: none;

  @include media-breakpoint-up(md) {
    padding: .5625rem .625rem;
    font-size: .9375rem;
    border-radius: 0;
    box-shadow: inset 0 -2px 0 rgba(66, 71, 83, .2);

    &::-webkit-input-placeholder { color: $gray-700; }
    &:-moz-placeholder { color: $gray-700; }
    &::-moz-placeholder { color: $gray-700; }
    &:-ms-input-placeholder { color: $gray-700; }
  }

  &:hover { cursor: pointer; }

  &:focus {
    box-shadow: none;

    @include media-breakpoint-up(md) {
      box-shadow: inset 0 -2px 0 rgba(66, 71, 83, .2);
    }
  }
}

.navbar-dark .navbar-form .form-control {

  @include media-breakpoint-up(md) {

    color: #fff;
    box-shadow: inset 0 -2px 0 rgba(255, 255, 255, .2);

    &::-webkit-input-placeholder { color: #fff; }
    &:-moz-placeholder { color: #fff; }
    &::-moz-placeholder { color: #fff; }
    &:-ms-input-placeholder { color: #fff; }
  }
}

.icon-search {
  display: block;
  font-size: 1.25rem;
}

.dropdown-toggle {

  &:hover { cursor: pointer; }

  &:after {
    display: none; // no BS caret
  }

  img {
    margin-right: .5rem;
    box-shadow: 0 0 0 4px $gray-700;
  }

  span { vertical-align: middle; }
}

.navbar-dark .dropdown-toggle img {
  box-shadow: 0 0 0 4px #fff;
}

.nav-dropdown-search {
  @include media-breakpoint-up(sm) {
    margin-top: 0;
  }
}

.navbar-toggleable-custom .navbar-nav .nav-dropdown-user {
  padding: 1.25rem 0 1.3125rem .5rem;
}

.dropdown-menu-user { top: 104%; }

// Scroll Nav
@include media-breakpoint-up(md) {

  .scroll-nav {
    position: fixed;
    top: 50%;
    right: 5%;
    z-index: 2;
    transform: translateY(-50%);

    a {
      width: 17px;
      height: 17px;
      color: #fff;
      background-color: #fff;
      border-radius: 50%;

      &.active {
        border: 4px solid rgba(255,255,255,.25);
      }
    }
  }
}


//
// =Header
// --------------------------------------------------

html,
body {
  width: 100%;
  height: 100%;
}

$custom-min-width: map-get($grid-breakpoints, 'md');
$custom-min-height: 37.5em; // 600

// static

.jumbotron {
  margin-bottom: 0;
  background: #663fb5 url(../img/bg-header.jpg) no-repeat center bottom;
  background-size: cover;
  border-radius: 0;
  
  @include media-breakpoint-down(sm) {
    padding-top: 6.25rem;
  }
  
  @include media-breakpoint-up(md) {
    height: 100%;
    padding: 0;
  }

  .btn {
    padding: 1rem 2rem 1.35rem;
    border-color: #ccc;

    &:hover,
    &:focus {
      background-color: rgba(255,255,255,0.5);
      border-color: transparent;
    }

    span:before {
      display: inline-block;
      transform: translateY(3px);
    }
  }

  a.jumbolink {
    opacity: .6;
    transition: opacity .175s;
  }

  a.jumbolink:hover,
  a.jumbolink:focus {
    opacity: 1;
  }
}

// slider

.carousel-header,
.carousel-inner,
.carousel-item {
  height: 100%;
}

.carousel-header {
  .carousel-indicators {

    @include media-breakpoint-up(md) {
      top: 50%;
      right: 30px;
      bottom: auto;
      left: auto;
      width: auto;
      margin-left: 0;
      transform: translateY(-50%);

      li {
        position: relative;
        display: block;
        margin: 0;
      }

      li + li {
        margin-top: 20px;

        &:before {
          position: absolute;
          bottom: 17px;
          left: 8px;
          width: 1px;
          height: 20px;
          content: '';
          background-color: rgba(255, 255, 255, .25);
        }
      }

      .active {

        &:before { bottom: 21px; }

        & + li:before { height: 16px; }
      }
    }

    li {
      width: 17px;
      height: 17px;
      margin: 10px;
      background-color: #fff;
      border: 0;
      border-radius: 50%;
    }

    .active {
      width: 17px;
      height: 17px;
      box-shadow: 0 0 0 4px rgba(255,255,255,.25);
    }
  }

  .carousel-item {
    height: 100%;
    background-repeat: no-repeat;
    background-position: 50%;
    background-size: cover;
  }

  .carousel-caption {
    top: 20%;
    bottom: auto;
    text-shadow: none;

    @include media-breakpoint-up(md) {
      top: 50%;
      right: 40px;
      left: 40px;
      padding: 0;
      text-shadow: none;
      transform: translateY(-50%);
    }
  }

  .btn { padding: 1.05rem 2rem 1.35rem; }
}

@include media-breakpoint-down(md) {
  h1,
  .h1 {
    margin-bottom: 1rem;
  }

  .display-3 {
    font-size: 2rem;
  }
}

h2 {
  font-weight: 300;
  line-height: 1.5;

  @include media-breakpoint-down(md) {
    font-size: 1.35rem;
  }

  @media (max-height: $custom-min-height - 1) {
    display: none;
  }

  em {
    font-weight: 500;
  }
}

.btn {
  padding: $input-btn-padding-y ($input-btn-padding-x - 2.25rem) ($input-btn-padding-y - .1rem);
  font-size: .8125rem;
  line-height: 1.1538;
  text-transform: uppercase;
  letter-spacing: .1rem;
  border-width: 1px;

  @include media-breakpoint-up(lg) {
    padding: $input-btn-padding-y ($input-btn-padding-x - 1.25rem) ($input-btn-padding-y - .1rem);
  }

  > i,
  > span {
    margin-right: .3rem;
    font-size: 1.28rem;
    line-height: 1;
    
    &:before {
      transform: translateY(5px);
    }
  }
}

.bg-dark .btn-secondary-outline {
  &:focus,
  &:hover {
    background-color: rgba(255, 255, 255, .5);
    border-color: transparent;
  }
}

.social-share {
  margin-bottom: 1rem;
  font-family: $font-family-sans-serif-custom;
  font-size: .8125rem;
  font-weight: 700;
  line-height: 1.23;
  text-align: center;

  @media (min-width: $custom-min-width) and (min-height: $custom-min-height) {
    position: absolute;
    right: 0;
    bottom: 35px;
    left: 0;
    margin-bottom: 0;
  }

  a {
    color: #fff;

    span {
      margin-right: .3rem;
      font-size: .9375rem;
      color: rgba(255, 255, 255, .5);
    }

    .icon-facebook { margin-right: .1rem; }

    &:focus,
    &:hover {
      span { color: #fff; }
    }
  }

  li { padding: .1875rem 0; }

  li + li {
    border-left: 1px solid rgba(255, 255, 255, .25);
  }
}


//
// =Intro
// --------------------------------------------------

h3 {
  margin-bottom: 1.25rem;
  font-weight: 400;
  line-height: 1.178;
}

.section-intro {
  overflow: hidden;

  .lead {
    margin-bottom: 2.5rem;
  }
}


//
// =Features
// --------------------------------------------------

.section-features {
  .card {
    padding-right: 0;
    padding-left: 0;
    margin-bottom: 3rem;
    border: 0 none;

    @include media-breakpoint-up(md) {
      margin-bottom: 0;
    }
  }

  .card-body {
    padding: 0;

    @include media-breakpoint-up(md) {
      padding: 0 1.25rem;
    }
  }
}

.card-body {
  > span {
    display: block;
    width: 90px;
    height: 90px;
    margin: 0 auto 20px;
    font-size: 2.125rem;
    line-height: 2.5;
    text-align: center;
    border: 5px solid #f5f5f5;
    border-radius: 50%;
  }

  .icon-heart {
    font-size: 1.85rem;
    line-height: 2.75;
  }
}

.card-title {
  margin-bottom: 5px;
  font-size: 2.25rem;
  font-weight: 600;
  line-height: 1.1666;
}

.card-subtitle {
  margin-bottom: .5rem;
  font-size: 1rem;
  font-weight: 300;
  line-height: 2;

  @include media-breakpoint-up(sm) {
    margin-bottom: 1.5625rem;

    &:after {
      display: block;
      width: 80%;
      height: 1px;
      margin: 30px auto 0;
      content: '';
      background-color: #ededed;
    }
  }
}

.card-text { font-weight: 300; }


//
// =Video
// --------------------------------------------------

figure { position: relative; }

.img-responsive-custom { width: 100%; }

.modal-video {
  width: auto;
  margin: .9375rem;

  @include media-breakpoint-up(md) {
    margin: .9375rem 1.875rem;
  }

  .modal-content {
    border: 0 none;
  }
}

.has-light-mask {
  position: relative;
  z-index: 1;

  &:after {
    position: absolute;
    top: 0;
    right: 0;
    z-index: 1;
    width: 100%;
    height: 100%;
    content: '';
    background-color: rgba(195,199,203,1);
    opacity: .6;
    transition: opacity .3s;
  }

  &:hover:after {
    opacity: 0;
  }
}

.image-effect {
  overflow: hidden;

  img {
    transition: transform .5s cubic-bezier(.7,0,.3,1);
    transform: scale3d(1.8, 1.8, 1);
  }

  &:hover img {
    transform: scale3d(1, 1, 1);
  }
}

.icon-play-button {
  position: absolute;
  top: 50%;
  left: 50%;
  z-index: 2;
  color: #fff;
  transform: translate(-50%, -50%);

  span { font-size: 3.125rem; }

  &:focus,
  &:hover {
    color: rgba(255,255,255,.5);
    outline: 0;
  }
}


//
// =Pricing
// --------------------------------------------------

.pricing-box {
  padding-bottom: 1.5625rem;
  font-weight: 300;
  background-color: #fcfefe;
  border: 1px solid #eceff3;

  .card-block {
    padding: 1rem;

    @include media-breakpoint-up(sm) {
      padding-top: 1.5625rem;
    }
  }

  .card-header {
    padding-top: 1.1rem;
    padding-bottom: 1.1rem;
    font-size: .8125rem;
    font-weight: 600;
    line-height: 1.1538;
    background-color: #eceff3;
    border-bottom: 0 none;

    &:first-child {
      border-radius: 0;
    }
  }

  .card-title {
    margin-bottom: .625rem;
    font-size: 1rem;
    font-weight: 300;
    line-height: 2;
  }

  h4 small {
    font-size: .8125rem;
    font-weight: 600;
    line-height: 1.1538;
    color: #aeb0b5 !important;
  }

  .list-group {
    margin-bottom: 1rem;

    @include media-breakpoint-up(sm) {
      margin-bottom: 4.0625rem;
    }
  }

  .list-group-item {
    padding: .45rem 1rem;
    background-color: transparent;
    border-color: #ededef;
  }

  .list-group-item:first-child {
    border-top-color: transparent;
  }

  .list-group-item:last-child {
    border-bottom-color: transparent;
  }

  .btn {
    min-width: 206px;

    @include media-breakpoint-up(md) {
      min-width: 175px;
    }
    @include media-breakpoint-up(lg) {
      min-width: 206px;
    }
  }
}

.pricing-box-currency,
.pricing-box-price {
  margin-right: -1rem;
  font-family: $font-family-sans-serif-custom;
  line-height: 1.214;
}

.pricing-box-currency {
  top: -1.5rem;
  font-size: 1.75rem;
  font-weight: 400;
  letter-spacing: .75rem;
}

.pricing-box-price {
  font-size: 4.25rem;
  font-weight: 700;
}

.pricing-best {
  background-color: #fff;
  border: 2px solid map-get($theme-colors, primary);

  @include media-breakpoint-up(md) {
    margin-right: -30px;
    margin-left: -30px;
  }

  .card-header {
    color: #fff;
    background-color: map-get($theme-colors, primary);
  }
}

.stacking-top {
  position: relative;
  z-index: 10;
}


//
// =Testimonials
// --------------------------------------------------

.section-testimonials {
  padding-bottom: 3rem;
  background: #663fb5 url(../img/bg-testimonials.jpg) no-repeat center bottom;
  background-size: cover;
  
  
  @include media-breakpoint-up(md) {
    padding-bottom: 2.5rem;
  }

  .blockquote {
    margin: 0 auto 2rem;
    border-left: 0 none;

    @include media-breakpoint-up(md) {
      max-width: 60rem;
      margin-bottom: 4.5rem;
    }

    > img {
      margin-bottom: 1.5625rem;
      border: 4px solid #fff;
    }

    p {
      margin-bottom: .9375rem;
      font-weight: 400;
      line-height: 1.464;

      &:before {
        content: '\201c';
      }

      &:after {
        content: '\201d';
      }
    }

    footer {
      font-size: 1rem;
      font-weight: 600;
      line-height: 1.1875;
      color: #fff;

      &:before {
        content: '';
      }

      &:after {
        display: block;
        width: 2.5rem;
        height: 1px;
        margin: .625rem auto 0;
        content: '';
        background-color: rgba(255, 255, 255, .25);
      }
    }
  }

  .carousel-indicators {
    position: inherit;
    bottom: auto;
    left: auto;
    width: auto;
    margin: 0;

    img {
      width: 45px;
      height: 45px;
      margin: 5.5px auto 0;
      border: 3px solid #fff;
      transition: all .1s ease-in-out;
    }

    li {
      width: 60px;
      height: 60px;
      margin: 0;
      text-indent: inherit;
      border: 0;
      border-radius: 0;
      background-color: transparent;
      opacity: .5;
      transition: opacity .2s;
      cursor: pointer;

      &:hover { opacity: .9; }

      &.active {
        margin: 0;
        background-color: transparent;
        opacity: 1;

        img {
          width: 56px;
          height: 56px;
          margin-top: 0;
          border: 4px solid #fff;
        }
      }
    }
  }
}


//
// =Text
// --------------------------------------------------

.separator-x {
  position: relative;

  &:before {
    position: absolute;
    top: 0;
    left: -20%;
    display: block;
    width: 1px;
    height: 100%;
    content: '';
    background-color: #ededed;
  }
}


//
// =News
// --------------------------------------------------

.section-news {
  article {
    max-width: 22.5rem;
    padding: 1rem;

    @include media-breakpoint-up(lg) {
      padding: 0;
    }
  }

  .icon-arrow-right {
    display: inline-block;
    margin-left: .25rem;
    font-size: .875rem;
    transition: transform .3s;
  }

  h5 {
    margin-bottom: 1rem;
    line-height: 1.777;

    a { display: block; }

    a:hover .icon-arrow-right {
      transform: translate3d(15px, 0, 0);
    }
  }
}

.badge {
  padding: .35em .85em;
  margin-bottom: .5rem;
  font-size: .8125rem;
  font-weight: 600;
  line-height: 1.1538;
  border-radius: 2.5rem;

  span {
    margin-right: .15rem;
    font-size: 16px;
    vertical-align: text-top;
  }
}

.badge-default {
  padding-right: 0;
  padding-left: 0;
  margin-right: .5rem;
  color: rgba(255,255,255,.6);
  background-color: transparent;
}


//
// =Sign up
// --------------------------------------------------

.form-group {
  margin-bottom: 1.875rem;
}

.has-icon-left {
  position: relative;

  &:before {
    position: absolute;
    top: 14px;
    left: 48px;
    width: 1px;
    height: 1.8125rem;
    content: '';
    background-color: #ededef;
  }

  &:after {
    position: absolute;
    top: 14px;
    left: 16px;
    z-index: 9999;
    width: 30px;
    height: 30px;
    content: '';
    background-repeat: no-repeat;
    background-position: 5% center;
  }

  .form-control { padding-left: 60px; }
}

.form-control-name:after {
  background-image: url('data:image/svg+xml;base64,');
}

.form-control-email:after {
  background-image: url('data:image/svg+xml;base64,');
}

.form-control-password:after {
  background-image: url('data:image/svg+xml;base64,');
}

.form-control {
  -webkit-appearance: none;
  border-width: 1px;

  &:focus {
    box-shadow: 0 0 4px rgba(135,121,189,.53);

    & + span {
      color: map-get($theme-colors, primary);
    }
  }
}

.form-control-lg {
  font-size: 1rem;
  line-height: 2;
}

.c-input {
  display: block;
  padding: 0;
  font-size: .9375rem;
  line-height: 1.2;
  color: rgba(187,190,196,.8);
  text-align: center;

  &:hover {
    cursor: pointer;
  }

  @include media-breakpoint-up(md) {
    text-align: left;
  }

  > input {
    position: absolute;
    z-index: -1;
    opacity: 0;
  }

  a {
    color: rgba(187,190,196,.8);
    text-decoration: underline;

    &:focus,
    &:hover {
      color: rgba(187,190,196,1);
    }
  }
}

.c-indicator {
  position: relative;
  top: 2px;
  display: inline-block;
  width: 20px;
  height: 20px;
  margin-right: .3rem;
}

.c-checkbox .c-indicator {
  background-color: #fff;
  border: 1px solid map-get($theme-colors, primary);
  border-radius: 0;
}

.c-checkbox input:checked ~ .c-indicator {
  background-image: url(data:image/svg+xml;base64,);
  background-repeat: no-repeat;
  background-position: 50%;
  background-size: 65%;
}

.c-input > input:checked ~ .c-indicator {
  background-color: map-get($theme-colors, primary);
}

.btn {
  cursor: pointer;
}

.btn-lg {
  padding: 1.36rem 1rem 1.266rem;
  border-radius: 2rem;
}


//
// =Footer
// --------------------------------------------------

.section-footer {
  padding-top: 4.4rem;
  padding-bottom: 3rem;

  @include media-breakpoint-up(md) {
    padding-bottom: 4.4rem;
  }

  small {
    font-size: .8125rem;
    line-height: 1.538;
  }

  .media {
    @include media-breakpoint-down(sm) {
      margin-bottom: 2rem;
      text-align: center;
    }
  }

  .media-body {
    display: block;
    margin: auto;
  }

  .icon-logo {
    padding-right: 1.75rem;

    @include media-breakpoint-down(sm) {
      padding: 0;
      margin-bottom: 1rem;
    }
  }

  .nav {
    font-size: .9375rem;
    font-weight: 600;
    line-height: 1.2;
    text-align: center;

    @include media-breakpoint-up(md) {
      text-align: right;
    }

    @include media-breakpoint-up(xl) {
      padding-top: 1.65rem;
    }

    .nav-item {
      padding-right: 1rem;
      padding-left: 1rem;

      @include media-breakpoint-down(sm) {
        display: block;
        text-align: center;
      }

      @include media-breakpoint-up(xl) {
        &:first-child {
          padding-left: 0;
        }

        &:last-child {
          padding-right: 0;
        }
      }

      &.active a {
        box-shadow: 0 2px 0 rgba(66, 71, 83, .2);
      }

      a {
        display: inline-block;
        padding: .5rem 0;
        
        @include media-breakpoint-down(lg) {
          padding: 1rem 0;
        }
      }
    }
  }

  .icon-caret-up {
    margin-left: .25rem;
    font-size: 20px;
    vertical-align: middle;

    @include media-breakpoint-down(sm) {
      display: block;
      margin: 1rem 0;
    }
  }
}

.section-footer.bg-dark {
  color: #fff;

  .nav-inline > li {
    &.active a {
      box-shadow: 0 2px 0 rgba(255, 255, 255, .2);

      &:focus,
      &:hover {
        color: #fff;
      }
    }

    a {
      &:focus,
      &:hover {
        color: rgba(255, 255, 255, .6);
      }
    }
  }
}


//
// =Profile card
// --------------------------------------------------

.card-social {
  background-color: #fff;
  border: 0 none;
  border-radius: 0;
  border-radius: .25rem;

  @include media-breakpoint-up(md) {
    .card-stat + .card-stat:before {
      position: absolute;
      top: 0;
      right: 100%;
      bottom: 0;
      width: 1px;
      height: 100%;
      content: '';
      background-color: #e9eaec;
    }
  }

  .card-body {
    padding-top: 1.875rem;
    padding-bottom: 1.875rem;

    &:first-child {
      border-radius: .25rem .25rem 0 0;
    }

    &:last-child {
      border-radius: 0 0 .25rem .25rem;
    }
  }

  img {
    margin-bottom: 1rem;
    box-shadow: 0 0 0 4px #fff;
  }

  .card-title { font-size: 1.125rem; }

  .card-subtitle {
    margin-bottom: 0;
    color: #fff;
    &:after {
      display: none;
    }
  }

  .btn {
    position: absolute;
    top: 1rem;
    right: 1rem;
    padding: .4rem .75rem;
    font-size: .875rem;
    font-weight: 300;
    color: #fff;
    text-transform: capitalize;
    letter-spacing: 0;
    border-color: #fff;
    &:hover,
    &:focus {
      background-color: rgba(255, 255, 255, .25);
      border-color: transparent;
    }
  }

  h4 {
    font-family: $font-family-sans-serif-custom;

    @include media-breakpoint-up(md) {
      margin-bottom: 0;
    }
  }

  small {
    display: block;
    font-family: $font-family-sans-serif;
    font-size: .8125rem;
    color: rgba(66,71,83,.4);
  }

  .card-stat { position: relative; }
}


//
// =Social buttons
// --------------------------------------------------

$facebook: #3b5998;
$twitter: #55acee;
$google: #dc4e41;
$linkedin: #0077b5;

.bg-facebook { background-color: $facebook; }
.bg-twitter { background-color: $twitter; }
.bg-google { background-color: $google; }
.bg-linkedin { background-color: $linkedin; }

.btn-social {
  color: #fff;
  border-radius: .25rem;
  &:focus,
  &:hover {
    color: #fff;
    background-color: $gray-700;
  }

  span:before { color: rgba(255,255,255,.5); }

  > .icon-linkedin {
    position: relative;
    top: -.2rem;
    font-size: 1.1rem;
  }
}

.btn-social-icon {
  padding: 1.4rem;
  background-color: #fff;

  > span {
    margin: 0;

    &:before {
      color: rgba(66,78,83,.5);
    }
  }

  &:hover,
  &:focus {

    > span:before {
      color: #fff;
    }
  }
}

.btn-social-facebook {
  &:hover,
  &:focus {
    background-color: $facebook;
  }
}

.btn-social-twitter {
  &:hover,
  &:focus {
    background-color: $twitter;
  }
}

.btn-social-google {
  &:hover,
  &:focus {
    background-color: $google;
  }
}

.btn-social-linkedin {
  &:hover,
  &:focus {
    background-color: $linkedin;
  }
}


//
// =Donut chart
// --------------------------------------------------

.card-chart {
  background-color: #fff;

  .list-group-item {
    padding-top: 1.035rem;
    padding-bottom: 1.0625rem;
    margin-bottom: 0;
    font-size: .8125rem;
    font-weight: 600;
    line-height: 1;
    color: rgba(65,70,83,.75);
    text-transform: uppercase;
    border: 0 none;
    border-top: 1px solid #e9eaec;
    border-radius: 0;

    &:first-child { border-top-color: transparent; }

    .label {
      padding: 0;
      margin: 0;
      font-family: $font-family-sans-serif-custom;
      font-size: 1.125rem;
      font-weight: 700;
      line-height: .7222;
      color: rgba(65, 70, 83, .8);
    }

    &.complete { background-color: $gray-100; }
  }
}

.icon-status {
  display: inline-block;
  width: 16px;
  height: 16px;
  margin-right: .5rem;
  vertical-align: text-bottom;
  border-radius: 50%;
}

.status-completed { background-color: #5e98e3; }

.status-backlog { background-color: #59d0bd; }

.status-noticket { background-color: #e8e9ec; }

#chart-holder {
  position: relative;
  width: 80%;
  padding-top: 2rem;
  padding-bottom: 2rem;

  &:before {
    position: absolute;
    top: 50%;
    left: 50%;
    display: block;
    width: 100%;
    font: 700 35px sans-serif;
    text-align: center;
    content: attr(data-active); /* Completed + In backlog */
    transform: translate(-50%,-50%);
  }

  canvas { width: 170px; }
}
